@import 'utilities/colors';
@import 'utilities/typography';

*, *:before, *:after {
    box-sizing: border-box;
    // outline: 1px solid pink;
}

body {
    background-color: #f5f9ff;
    position: relative;
    height: 100vh;
    font-size: 16px;
    display: block;
    margin: 0;
    text-align: center;
    font-family: Circular, "Helvetica Neue", Arial, Helvetica, sans-serif;
}

h1 {
    font-size: 24px;
}

header {
    display: block;
    margin: 0 auto;
    text-align: center;
    padding: 24px;

    img {
        max-width: 200px;
    }
}

main {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 24px;
}

.card {
    background-color: #fff;
    border-radius: 8px;
    border: 1px solid #e3e6eb;
    box-shadow: 0 4px 16px rgba(97, 152, 193, 0.1);
    padding: 24px;
}

footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 24px;

    img {
        z-index: 1;
        margin-bottom: 24px;
    }

    &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        background-color: #e3e6eb;
        height: 140px;
        width: 100%;
        z-index: -1;
    }
}

img {
    max-width: 100%;
}